.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 80px;
  width: auto;
  pointer-events: none;
  border: solid black;
  border-radius: 50px;
  margin-left: 10px;
}

.App-title{
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-bottom: -35px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  justify-content: center;
  padding: 10px 10px 0px 10px;
}

.App-button {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #ed3330;
  padding: 5px;
  border-radius: 20px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.App-button:hover {
  background: #434343;
  letter-spacing: 1px;
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  box-shadow: 5px 40px -10px rgba(0,0,0,0.57);
  transition: all 0.4s ease 0s;
  cursor: pointer;
}

.App-body{
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-width{
  align-items: center;
  max-width: 70%;
}

.App-link {
  color: #61dafb;
}

.App-flexRow{
  align-items: center;
  list-style: none;
  flex-direction: row;
  display: flex;
}

.App-flexLi{
  padding: 5px;
}