.nameList {
    padding: 0;
    list-style-type: none;
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.nameLi {
    flex: 1 0 10%;
    padding: 1.5px;
    flex-grow: 0;
}

.inputArea{
    flex-direction: column;
    align-items: center;
    display: flex;
    z-index: 300;
    justify-content: center;  
}

.input{
    border: 4px solid #ee0d0d;
    border-radius: 5px;
    width: 80%;
    max-width: 300px;
    padding: 15px 22px;
    margin: 10px 5px;
    font-size: large;
    text-align: center;
}


.inputButton{
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #ed3330;
    padding: 20px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    transition: all 0.4s ease 0s;
}

.inputButton:hover {
    background: #434343;
    letter-spacing: 1px;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    box-shadow: 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.4s ease 0s;
    cursor: pointer;
}

.listItem {
    border: solid #ee0d0d;
    border-width: 3px;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
}
